.app__navbar {
    width: 100%;
    display: flex;
justify-content: space-between;
    align-items: center;
   background: var(--color-black);
   

}
.app__navbar-logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.app__navbar-logo img {
    width:200px;

}
.app__navbar-links{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    width: 40pc;
}
.app__navbar-links li{
    margin: 0 1rem;
    cursor: pointer;
    color:rgb(102, 99, 90);
    
}
.app__navbar-links li:hover {
  color:var(--color-grey) ;
}
.app__navbar-login{
    display: flex;
    justify-content: flex-end;
    align-items: center;
color: aliceblue;
width: 20pc;
}
.app__navbar-login a{
    margin: 0 1rem;
    text-decoration:none;
    transition: 0.5s ease;

}
.app__navbar-login a:hover{
    border-bottom: 1px solid var(-color-golden);
}

.app__navbar-login div{
    width: 1px;
    height: 30px;
    background-color: var(--color-grey);
 
}


.app__navbar-smallscreen_overlay{
    position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: var(--color-black);
transition: 0.5s ease;
flex-direction: column;
z-index: 5;
}
.app__navbar-smallscreen_overlay .overlay__bg{
    font-size: 27px;
    color:var(--color-golden);
    cursor: pointer;
position: absolute;
top: 20px;
right: 20px;
}
.app__navbar-smallscreen_links{
    list-style: none;
     text-align: center;
}
.app__navbar-smallscreen_links li{
    margin: 2rem;
 color: var(--color-golden);   
    cursor: pointer;
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
    height: 5pc;
    width: 100pc;

}
.app__navbar-smallscreen_links li:hover{
    color: var(--color-white);
}
.app__navbar-smallscreen{
    overflow: hidden;
    position: relative;
}

@media screen and (max-width: 2000px){
.app__navbar-logo img{
    width: 210px;
}
}
@media screen and (max-width: 650px){
.app__navbar{
    padding: 1rem;
}
.app__navbar-login{
    display: none;
}
.app__navbar-logo img{
    width: 110px;
}
}

@media screen and (max-width: 1150px){
    .app__navbar-links{
        display: none;
    }


}


